<template>
  <div name="ojbk">
    <el-dialog
      title=""
      :visible.sync="showDetail"
      width="80%"
      :before-close="handleClose"
      ><div class="infoShowBox">
        <el-row :gutter="20">
          <el-col :span="16">
            <el-alert
              title=""
              :description="info.title"
              type="info"
              :closable="false"
            >
            </el-alert
          ></el-col>
          <el-col :span="8">
            <el-alert
              title=""
              :description="info.companyName"
              type="info"
              :closable="false"
            >
            </el-alert
          ></el-col>
        </el-row>

        <el-alert
          title=""
          :description="info.companyDec"
          type="info"
          :closable="false"
        >
        </el-alert>
        <el-row :gutter="20">
          <el-col :span="12">
            <el-alert
              title=""
              :description="info.recruitDate"
              type="info"
              :closable="false"
            >
            </el-alert
          ></el-col>
          <el-col :span="12">
            <el-alert
              title=""
              :description="info.addr"
              type="info"
              :closable="false"
            >
            </el-alert
          ></el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="6">
            <el-alert
              title=""
              :description="info.contractPerson"
              type="info"
              :closable="false"
            >
            </el-alert
          ></el-col>
          <el-col :span="10">
            <el-alert
              title=""
              :description="info.email"
              type="info"
              :closable="false"
            >
            </el-alert
          ></el-col>
          <el-col :span="8">
            <el-alert
              title=""
              :description="info.mobile"
              type="info"
              :closable="false"
            >
            </el-alert
          ></el-col>
        </el-row>
      </div>
      <el-alert
        title=""
        type="success"
        :closable="false"
        style="margin-bottom: 20px"
      >
      </el-alert>
      <el-table :data="list" style="width: 100%">
        <el-table-column prop="recruitPos" label="" width="180">
        </el-table-column>
        <el-table-column prop="demandPos" label="" width="180">
        </el-table-column>
        <el-table-column prop="address" label=""> </el-table-column>
        <el-table-column prop="person" label=""> </el-table-column>
        <el-table-column prop="addAt" label=""> </el-table-column>
      </el-table>
      <span slot="footer" class="dialog-footer">
        <!-- <el-button @click="dialogVisible = false"> </el-button> -->
        <el-button type="primary" @click="handleClose"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "ojbk",
  props: {
    showDetail: {
      type: Boolean,
      required: true,
      defaule: false,
    },
    info: {
      type: Object,
      required: true,
    },
    list: {
      type: Array,
      required: true,
      default: [],
    },
  },
  components: {},
  data() {
    return {
      jobList: [
        {
          recruitPos: "",
          demandPos: "",
          address: "",
          person: "",
          addAt: "",
        },
      ],
      dialogVisible: false,
    };
  },
  created() {},
  methods: {
    handleClose() {
      this.$emit("closeDetail");
    },
  },
  computed: {},
  mounted() {},
};
</script>

<style scoped >
/* :nth-child()  */
.infoShowBox > :nth-child(n) {
  margin-bottom: 20px;
}
</style>
