<template>
  <div name="ojbk">
    <el-dialog
      title=""
      :visible.sync="showAddInfo"
      width="70%"
      :before-close="handleClose"
    >
      <el-form ref="addform" :model="form" label-width="80px" :rules="rules">
        <el-form-item label="" prop="title">
          <el-input v-model="form.title"></el-input>
        </el-form-item>
        <el-form-item label="" prop="companyName">
          <el-input v-model="form.companyName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="companyDec">
          <el-input type="textarea" v-model="form.companyDec"></el-input>
        </el-form-item>
        <el-form-item label="" prop="recruitDate">
          <el-date-picker
            v-model="form.recruitDate"
            type="datetime"
            placeholder=""
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="addr">
          <el-input v-model="form.addr"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="form.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="contractPerson">
          <el-input v-model="form.contractPerson"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="form.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose"> </el-button>
        <el-button type="primary" @click="submit"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { postAction } from "@/api/api";
export default {
  name: "ojbk",
  props: {
    showAddInfo: {
      type: Boolean,
      required: true,
      defaule: false,
    },
  },
  components: {},
  data() {
    return {
      form: {
        title: "",
        companyName: "",
        companyDec: "",
        recruitDate: "",
        addr: "",
        email: "",
        contractPerson: "",
        mobile: "",
      },
      rules: {
        title: [{ required: true, message: "", trigger: "blur" }],
        companyName: [
          { required: true, message: "", trigger: "blur" },
        ],
        companyDec: [
          { required: true, message: "", trigger: "blur" },
        ],
        addr: [{ required: true, message: "", trigger: "blur" }],
        email: [{ required: true, message: "", trigger: "blur" }],
        contractPerson: [
          { required: true, message: "", trigger: "blur" },
        ],
        mobile: [{ required: true, message: "", trigger: "blur" }],
        recruitDate: [
          {
            type: "date",
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
      },
    };
  },
  created() {},
  methods: {
    getYMDHMS(date) {
      let Y = date.getFullYear();
      let x = date.getMonth() + 1;
      let M = x < 10 ? "0" + x : x;
      let D = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
      let YMD = Y + "-" + M + "-" + D;
      let h = date.getHours() < 10 ? "0" + date.getHours() : date.getHours();
      let m =
        date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
      let s =
        date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
      let HMS = h + ":" + m + ":" + s;
      return YMD + " " + HMS;
    },
    submit() {
      this.$refs.addform.validate(async (valid) => {
        if (valid) {
          this.form.recruitDate = this.getYMDHMS(this.form.recruitDate);
          let res = await postAction("/recruitinfo/add", this.form);
          console.log(res);
          if (res.code == 201) {
            this.$message.success("");
          } else {
            this.$message.error("");
          }
          this.$emit("closeAddInfo");
        } else {
          this.$message.error("");
        }
      });
    },
    handleClose() {
      this.$emit("closeAddInfo");
    },
  },
  computed: {},
  mounted() {},
};
</script>

<style scoped >
.el-form-item * {
  float: left;
}
</style>
